
.sc-work-page{
    padding: 50px 0;
    position: relative;
}

.sc-work-page .h1-title-box:before {
    content: "Work";
}

.sc-work-page h4{
    font-size: 16px;
    color: #181818;
    background-color: #fed9ca;
    padding: 5px 10px;
    border-radius: 5px;
    display: inline-block;
}
@media (max-width: 576px) {
    .sc-work-page h4{
     display: none;
    }
}